window.onload = function () {
  menuEventHelper();
};

function menuEventHelper() {
  const header = document.getElementById("inner_cont");
  const subMenus = document.getElementById("sub-menus");
  const subCenter = document.getElementById("sub-center");
  const mainMenu = document.getElementsByClassName("main_menu");
  const subMenu = document.getElementsByClassName("sub_menu");

  header.addEventListener("mouseover", () => {
    subMenus.style.visibility = "visible";
    subMenus.style.height = "190px";
    subMenus.style.color = "black";
    subCenter.style.display = "flex";
    Array.from(subMenu).map(
      (menu) => (menu.children[0].style.color = "#333333")
    );
  });
  header.addEventListener("mouseleave", () => {
    subMenus.style.visibility = "none";
    subMenus.style.height = "0px";
    subMenus.style.color = "transparent";
    subCenter.style.display = "none";
    Array.from(subMenu).map(
      (menu) => (menu.children[0].style.color = "transparent")
    );
  });

  Array.from(mainMenu).map((menu) => {
    menu.addEventListener("mouseover", function (e) {
      e.target.parentNode.classList.add("active");
    });
    menu.addEventListener("mouseleave", function (e) {
      e.target.parentNode.classList.remove("active");
    });
  });
}
